.App {
  font-family: 'Roboto', sans-serif;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  color: #333333;
  background-color: #f5f5f5;
}

.Header {
    padding: 20px;
}

h1, h2, h3 {
  font-weight: bold;
  margin-bottom: 5px;
}

/* Style paragraphs */
p {
  margin-bottom: 5px;
}

/* Style links */
a {
  color: #007bff; /* Accent color */
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

ul, ol {
  margin-bottom: 20px;
  padding-left: 20px; /* Indent list items */
}

li {
  margin-bottom: 5px; /* Adjust spacing between list items */
}

.Main {
  /* max-width: 800px; */
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.headerText {
  text-align: center;
}

.steps {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 250px;
  align-items: first baseline;
  margin-left: 0px;
}

.step {
  width: 12px;
  height: 12px;
  background: #fff;
  border: 2px solid #ACACA6;
  border-radius: 50%;
  margin-right: 10px;
  /* flex-grow: 1; */
}

.step-text {
  flex: 1;
  padding-right: 30px;
}